<template>
    <div style="background-color: rgb(239, 239, 239); padding: 20px">
        <!-- 搜索区 -->
        <el-form
            inline
            style="
                background-color: white;
                height: 86px;
                display: flex;
                align-items: center;
                padding: 20px;
                box-sizing: border-box;
            "
        >
            <el-form-item label="公司名">
                <el-input v-model="data.companyName" placeholder="请输入用户名"></el-input>
            </el-form-item>
            <el-form-item label="负责人">
                <el-input v-model="data.head" placeholder="请输入岗位"></el-input>
            </el-form-item>
            <el-form-item label="培训老师">
                <el-input v-model="data.trainTeachers" placeholder="请输入名称"></el-input>
            </el-form-item>
            <el-form-item label="联系方式">
                <el-input v-model.number="data.tel" placeholder="请输入手机号码"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="search">查询</el-button>
                <el-button type="primary" @click="reset">重置</el-button>
                <el-button type="primary" @click="addData">新增</el-button>
            </el-form-item>
        </el-form>
        <!-- 表格 -->
        <el-table :data="tableData" style="width: 100%">
            <el-table-column
                prop="companyName"
                label="公司名称"
                width="200px"
                align="center"
            ></el-table-column>
            <el-table-column
                prop="head"
                label="负责人 "
                width="120px"
                align="center"
            ></el-table-column>
            <el-table-column prop="tel" label="手机号" align="center"></el-table-column>
            <el-table-column
                prop="trainTeachers"
                label="培训老师"
                width="120px"
                align="center"
            ></el-table-column>
            <el-table-column prop="license" label="营业执照 " align="center" width="150px">
                <template #default="{ row }">
                    <div style="display: flex; align-items: center">
                        <el-image
                            :src="row.license[0]"
                            :preview-src-list="row.license"
                            preview-teleported
                            hide-on-click-modal
                        />
                    </div>
                </template>
            </el-table-column>
            <el-table-column prop="registrTime" label="注册时间" align="center"></el-table-column>
            <el-table-column prop="expireTime" label="到期时间" align="center"></el-table-column>
            <el-table-column>
                <template #default="{ row }">
                    <span @click="editData(row)">编辑</span>
                    <span @click="() => router.push('/eventManagement')">活动</span>
                    <span @click="() => router.push('/')">报表</span>
                    <span @click="() => router.push('/')">下载二维码</span>
                    <el-switch></el-switch>
                </template>
            </el-table-column>
        </el-table>
        <!-- 分页器 -->
        <el-pagination
            v-model:current-page="paginator.currentPage"
            v-model:page-size="paginator.pageSize"
            :page-sizes="[16, 26, 36, 46]"
            layout="->,total, sizes, prev, pager, next, jumper"
            :total="paginator.total"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            style="background-color: white; height: 68px; padding-right: 20px"
        />
    </div>
    <!-- 印业执照 -->
    <!-- 编辑 -->
    <dialog-edit-box
        :loading="showEdit"
        title="编辑"
        :structure="structure"
        @cance-edit="canceEdit"
        @save-edit="saveEdit"
        :row-data="rowData"
    ></dialog-edit-box>
    <!-- 活动查看 -->
    <!-- 查看报表 -->
    <!-- 查看  -->
</template>

<script setup lang="ts">
import { ref, reactive, onMounted } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import http from '@/util/http'
/**
 * 路由对象
 */
const route = useRoute()
/**
 * 路由实例
 */
const router = useRouter()
//console.log('1-开始创建组件-setup')
/**
 * 数据部分
 */
const data = reactive({
    companyName: '',
    head: '',
    trainTeachers: '',
    tel: ''
})

// 弹窗组件结构数据
const structure = ref([
    {
        label: '公司名',
        prop: 'companyName',
        type: 'input',
        placeholder: '请输入公司名',
        style: 'width: 300px'
    },
    {
        label: '负责人',
        prop: 'head',
        type: 'input',
        placeholder: '请输入负责人',
        style: 'width: 300px'
    },
    {
        label: '培训老师',
        prop: 'trainTeachers',
        type: 'input',
        placeholder: '请输入培训老师',
        style: 'width: 300px'
    },
    {
        label: '联系方式',
        prop: 'tel',
        type: 'input',
        placeholder: '请输入联系方式',
        style: 'width: 300px'
    },
    {
        label: '印业执照',
        prop: 'license',
        type: 'img',
        placeholder: '请添加营业执照',
        style: 'width: 300px'
    },
    {
        label: '注册时间',
        prop: 'registrTime',
        type: 'date',
        placeholder: '请添加注册时间',
        style: 'width: 300px'
    },
    {
        label: '到期时间',
        prop: 'expireTime',
        type: 'date',
        placeholder: '请添加到期时间',
        style: 'width: 300px'
    }
])

// 表格数据
const tableData = ref()

// 搜索表单数据
const search = async () => {
    const { data } = await http({
        url: '/masage',
        method: 'POST'
    })
    tableData.value = data
}
// 弹窗组件显示
let showEdit = ref<boolean>(false)

// 分页器
const paginator = ref({
    currentPage: 1,
    pageSize: 16,
    total: 100
})

// 点击分页器
const handleSizeChange = (val: number) => {
    console.log(`每页 ${val} 条`)
}

// 切换分页
const handleCurrentChange = (val: number) => {
    console.log(`当前页: ${val}`)
}

// 子组件待编辑数据
const rowData = ref({})

// 新增数据
const addData = () => {
    rowData.value = {}
    showEdit.value = true
}

// 编辑数据
const editData = (row: any) => {
    rowData.value = row
    showEdit.value = true
}

// 取消编辑、新增
const canceEdit = () => {
    showEdit.value = false
    rowData.value = {}
}
// 保存编辑、新增
const saveEdit = (data: any) => {
    alert(JSON.stringify(data))
}
// 重置表单
const reset = () => {}

// 第一次DOM元素挂载
onMounted(() => {
    search()
})
</script>
<style scoped lang="scss"></style>
